            title="电费成本分析"
            :visible.sync="centerDialogVisible"
            width="72%"
            :close-on-click-modal="false"
            :modal="false"
            center
            v-dialogDrag
            @opened="open()"
          >
            <el-row id="title">
              <span class="demonstration"> 报表类型:</span>
              <!-- 图表内容 label不设置则等同于value的值-->
              <el-select v-model="value2" placeholder="选择报表">
                <el-option
                  v-for="item in options2"
                  :key="item.value"
                  :value="item.value"
                >
                </el-option>
              </el-select>
              <!-- 月选择器 -->
              <el-date-picker
                :style="areshowmonth"
                v-model="monthPick"
                type="month"
                placeholder="选择月"
              >
              </el-date-picker>
              <!-- 年选择器 -->
              <el-date-picker
                :style="areshowyear"
                v-model="yearPick"
                type="year"
                placeholder="选择年"
              >
              </el-date-picker>
              <!-- 电量电费选择 -->
              <el-select v-model="value1" placeholder="电费电量">
                <el-option
                  v-for="item in options1"
                  :key="item.value"
                  :value="item.value"
                >
                </el-option>
              </el-select>
              <el-button
                type="primary"
                plain
                size="medium"
                @click="changeTable()"
                >确定</el-button
              >
              <!-- 导出功能 -->
              <el-dropdown @command="handleCommand">
                <el-button type="primary" plain size="medium">
                  导出<i class="el-icon-arrow-down el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item v-print="'#myChart00'"
