  methods: {
    //echart赋值
    charge1() {
      let i = 0;
      while (i < this.totaldata.length) {
        //时间戳变量转化成时间变量
        var date = new Date(
          this.totaldata[i].dataTime * 1000 + 8 * 3600 * 1000
        );
        var daytime = date
          .toJSON()
          .substr(0, 19)
          .replace("T", " ")
          .replace(/-/g, ".");
        console.log(daytime);
        // console.log(this.totaldata.length)
        this.option11.xAxis.data[i] = daytime.substring(5, 10); //给X轴赋值
        this.option11.series[0].data[i] = this.totaldata[i].highKWh; //给峰电量赋值
        this.option11.series[1].data[i] = this.totaldata[i].lowKWh; //给谷电量赋值
        // this.option11.series[2].data[i] = this.totaldata[i].midKWh; //给平电量赋值
        this.option11.series[3].data[i] = this.totaldata[i].midKWh; //给尖电量赋值

        this.option12.xAxis.data[i] = daytime.substring(5, 10); //给X轴赋值
        this.option12.series[0].data[i] = this.totaldata[i].highCharge; //给峰电费赋值
        this.option12.series[1].data[i] = this.totaldata[i].lowCharge; //给谷电费赋值
        // this.option12.series[2].data[i] = this.totaldata[i].midCharge; //给平电费赋值
        this.option12.series[3].data[i] = this.totaldata[i].midCharge; //给尖电费赋值

        this.option13.xAxis.data[i] = daytime.substring(5, 10); //给X轴赋值
        this.option13.series[0].data[i] = this.totaldata[i].highCharge; //给峰电费赋值
        this.option13.series[1].data[i] = this.totaldata[i].lowCharge; //给谷电费赋值
        this.option13.series[2].data[i] = this.totaldata[i].midCharge; //给平电费赋值
        this.option13.series[3].data[i] = this.totaldata[i].midCharge; //给尖电费赋值

        this.option4.xAxis.data[i] = daytime.substring(5, 10); //给X轴赋值
        this.option4.series[0].data[i] = this.totaldata[i].highKWh; //给峰电量赋值
        this.option4.series[1].data[i] = this.totaldata[i].lowKWh; //给谷电量赋值
        this.option4.series[2].data[i] = this.totaldata[i].midKWh; //给平电量赋值
