            // this.tableData1[i].deviceId1 = this.TABLEdata[i];
            let flag = 0;
            for (var key in this.tableData1[i]) {
              if (key != "deviceId1") {
                this.tableData1[i][key] = daypowerList[i][flag].totalKwh;
                flag += 1;
              }
            }

            (this.tableData1[i].deviceId1 = daypowerNameList[i]), (i = i + 1);
          }
        }
        if (this.value1 == "电费") {
