    // 打印导出
    handleCommand(command) {
      if (command == "a") this.getPdf();
      if (command == "b") this.getPdf1();
    },
    //数据写入
    convert() {
      axios
        .post(
          "http://121.43.111.195/powerRealtime/findPowerDistributionDayByDataTime",
          {
            deviceId: "1",
            dataTime: "1638288000",
          }
        )
        .then((response) => {
          this.totaldata = response.data;
          //console.log(this.totaldata.length)
          console.log(this.totaldata);
          this.charge1();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //合计功能
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总价";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          sums[index] += " 元";
        } else {
          sums[index] = "N/A";
        }
      });

      return sums;
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    //echart
    open() {
      console.log("open");
      this.draw00();
      this.draw11();
      this.draw22();
      this.draw33();
    },
    draw00() {
      // 初始化echarts实例
      let myChart00 = this.$echarts.init(document.getElementById("myChart00"));
      // setTimeout(function () {
      //   window.onresize = function () {
      //     twChart.resize();
      //   };
      // }, 200);

      //防止越界，重绘canvas
      window.onresize = myChart00.resize;
      myChart00.setOption(this.option11); //设置option
    },

    draw11() {
      // 初始化echarts实例
      let myChart11 = this.$echarts.init(document.getElementById("myChart11"));
      // 绘制图表

      //防止越界，重绘canvas
      window.onresize = myChart11.resize;
      myChart11.setOption(this.option12); //设置option
    },
    draw22() {
      // 初始化echarts实例
      let myChart22 = this.$echarts.init(document.getElementById("myChart22"));
     

      //防止越界，重绘canvas
      window.onresize = myChart22.resize;
      myChart22.setOption(this.option13); //设置option
    },
    draw33() {
      // 初始化echarts实例
      let myChart33 = this.$echarts.init(document.getElementById("myChart33"));
     
      //防止越界，重绘canvas
      window.onresize = myChart33.resize;
      myChart33.setOption(this.option4); //设置option
    },
  },
};
</script>
