<!-- 耗电时比分析APP-->
<template>
  <div>
    <!-- APP按钮 -->
    
    <div class="buttonBackground" id="btn_normal">
      <button   type="text" @click="OPEN()"></button>
      <p>耗电时比分析</p>
    </div>
    <el-dialog title="耗电时比分析" :visible.sync="timeCompareVisible" width="70%"
