      var date = new Date(A*1000 + 8 * 3600 * 1000);
      var B = date.toJSON().substr(0, 19).replace('T', ' ').replace(/-/g, '.')
      return B
    },

  //------------------------------------------图数据处理----------------------------------------
    //在打开时显示
    initEcharts(){
      let newPromise = new Promise((resolve) => {
        resolve()
      })
    //然后异步执行echarts的初始化函数
      newPromise.then(() => {
        //  此dom为echarts图标展示dom
        let myChartCompare = echarts.init(document.getElementById("myChartCompare"), "light"); // 初始化echarts, theme为light
        myChartCompare.setOption(this.echartsOption);
    })
    },

    addLineCompare(){
      //给echarts的legend赋值
      var templist=[]
      var i=0

      while(i<this.checkListCompare.length){
        if(this.firstTimeValue !=null)
          templist[2*i]=this.firstTimeValue + this.checkListCompare[i]
        if(this.secondTimeValue !=null)
          templist[2*i+1]=this.secondTimeValue + this.checkListCompare[i]
        i=i+1
      }
      this.echartsOption.legend.data=templist

      //给echarts的select初始化,在确认按钮中也应该初始化

      //先将所有的selected键的值变为false
      i=0
      for(i=0;i<this.echartsOption.series.length;i++){
        var key = this.echartsOption.series[i].name
        this.echartsOption.legend.selected[key] = false

      }
    
      //再将所有的选中目标变为true
      i=0
      for(i=0;i<templist.length;i++){
        var key = templist[i]
        this.echartsOption.legend.selected[key] = true
      }

      

      let myChartCompare = echarts.init(document.getElementById("myChartCompare"), "light"); // 初始化echarts, theme为light
      myChartCompare.setOption(this.echartsOption);
