    dlclick(){
      this.changeBG=[0,0,1]
      this.isshowTable1 = 'display:none'
      this.isshowTable2 = 'display:none'
      this.isshowTable3 = ''
      this.initDLTable()
      if(this.firstTimeValue != null){
        this.originTitle[0]=this.firstTimeValue
        let i=0
        while(i<this.tableDataDLFirst.length){
          this.originData[i].a = this.tableDataDLFirst[i]
          i++
        }

      }
      if(this.secondTimeValue != null){
        this.originTitle[1]=this.secondTimeValue
        let i=0
        while(i<this.tableDataDLSecond.length){
          this.originData[i].b = this.tableDataDLSecond[i]
          i++
        }
      }

      

      //获取数据之后，进行矩阵转化.
      let matrixData = this.originData.map((row) => {
        let arr = []
        for (let key in row) {
          arr.push(row[key])
        }
        return arr
      })
    
      this.tableDataDl = matrixData[0].map((col, i) => {
        return [this.originTitle[i], ...matrixData.map((row) => {
          return row[i]
        })]
      })
      
    },



